<template>
  <v-content>
    <div>
      <h3>Onboarding</h3>
    </div>
    <div>Your language is {{ language }}</div>
    <div>Note: this page is under contruction.</div>
    <div class="ma-4" v-for="service in services" :key="service.value">
      <v-btn block color="orange" @click="useConfig(service.value)">{{ service.name }}</v-btn>
    </div>
  </v-content>
</template>

<script>

import flavors from '../config';

export default {
  name: "Onboarding",
  data() {
    return {
      loadFont: false,
    };
  },
  computed: {
    services() {
      return Object.keys(flavors).map(function(key) {
        return { name: flavors[key].displayName, value: key };
      });
    },
    language: function() {
      var lang = navigator.language || navigator.userLanguage;
      return lang;
    }
  },
  methods: {
    useConfig(config) {
      this.$store.commit("setFlavor", config);
      this.done();
    },

    done() {
      this.$root.onboarded = true;
      this.$root.$router.replace("/");
    }
  }
};
</script>

<style scoped>
@import url("../assets/css/item-style.css");
</style>